<template>
  <div v-if="product" class="product-detail">
    <h1>{{ product.title }}</h1>
    <img :src="product.image" alt="product.title" class="product-detail-image" />
    <p class="product-detail-price">Цена: ${{ product.price.toFixed(2) }}</p>
    <p class="product-detail-category">Категория: {{ product.category }}</p>
    <p class="product-detail-rating">Оценка: {{ product.rating.rate }}</p>
    <p class="product-detail-description">Описание: {{ product.description }}</p>
    <p class="product-detail-count">Число оценок: {{ product.rating.count }}</p>
    <button @click="addToCart(product)" class="add-to-cart-button">Добавить в корзину</button>
    <router-link to="/" class="back-button">Назад</router-link>
  </div>
  <div v-else>Загрузка...</div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      product: null
    }
  },
  methods: {
    async fetchProduct () {
      const response = await axios.get(`https://fakestoreapi.com/products/${this.$route.params.id}`)
      this.product = response.data
    },
    addToCart (product) {
      const cart = JSON.parse(localStorage.getItem('cart')) || []
      const existingProduct = cart.find(item => item.id === product.id)
      if (existingProduct) {
        existingProduct.quantity += 1
      } else {
        cart.push({ ...product, quantity: 1 })
      }
      localStorage.setItem('cart', JSON.stringify(cart))
    }
  },
  mounted () {
    this.fetchProduct()
  }
}
</script>

<style scoped>
.product-detail {
  max-width: 600px;
  margin: 20px auto;
  text-align: center;
}

.product-detail-image {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.product-detail-price,
.product-detail-category,
.product-detail-rating,
.product-detail-description,
.product-detail-count {
  margin: 10px 0;
}

.add-to-cart-button {
  background-color: #fbcc4a;
  color: white;
  border: none;
  padding: 13px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-right: 10px;
}

.add-to-cart-button:hover {
  background-color: #fbcc4a;
}

.back-button {
  margin-top: 20px;
  display: inline-block;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  text-decoration: none;
}

.back-button:hover {
  background-color: #0056b3;
}
</style>
