<template>
  <div class="cart">
    <h1>Корзина</h1>
    <div v-if="cart.length === 0">Корзина пуста</div>
    <div v-else>
      <div class="cart-item" v-for="item in cart" :key="item.id">
        <h2>{{ item.title }}</h2>
        <p>Количество: {{ item.quantity }}</p>
        <p>Цена: ${{ item.price.toFixed(2) }}</p>
      </div>
      <h2>Общая стоимость: ${{ totalPrice.toFixed(2) }}</h2>
      <button @click="clearCart" class="clear-cart-button">Очистить корзину</button>
    </div>
    <router-link to="/" class="back-button">Назад к товарам</router-link>
  </div>
</template>

<script>
export default {
  data () {
    return {
      cart: []
    }
  },
  computed: {
    totalPrice () {
      return this.cart.reduce((total, item) => total + item.price * item.quantity, 0)
    }
  },
  methods: {
    clearCart () {
      localStorage.removeItem('cart')
      this.cart = []
      alert('Корзина очищена!')
    }
  },
  mounted () {
    this.cart = JSON.parse(localStorage.getItem('cart')) || []
  }
}
</script>

<style scoped>
.cart {
  max-width: 600px;
  margin: 20px auto;
  text-align: center;
}

.cart-item {
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
}

.clear-cart-button {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 20px;
}

.clear-cart-button:hover {
  background-color: #c82333;
}

.back-button {
  margin-top: 20px;
  display: inline-block;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  text-decoration: none;
}

.back-button:hover {
  background-color: #0056b3;
}
</style>
