<template>
  <div class="MyHome">
    <h1>Товары</h1>

    <div>
      <label for="category">Фильтр по категории:</label>
      <select v-model="selectedCategory" @change="fetchProducts">
        <option value="">Все</option>
        <option value="jewelery">Украшения</option>
        <option value="men's clothing">Мужская одежда</option>
        <option value="electronics">Электроника</option>
        <option value="women's clothing">Женская одежда</option>
        <option v-for="category in categories" :key="category" :value="category">{{ category }}</option>
      </select>
    </div>

    <div v-if="loading">Загрузка...</div>

    <div v-if="!loading" class="product-list">
      <div class="product-card" v-for="product in paginatedProducts" :key="product.id">
        <router-link :to="`/product/${product.id}`" class="product-link">
          <div class="image-wrapper">
            <img :src="product.image" alt="product.title" class="product-image" />
            <div class="image-overlay">
              <div class="overlay-text">Посмотреть детали</div>
            </div>
          </div>
          <h2 class="product-title">{{ product.title }}</h2>
          <p class="product-price">Цена: ${{ product.price.toFixed(2) }}</p>
          <p class="product-category">Категория: {{ product.category }}</p>
          <p class="product-rating">Оценка: {{ product.rating.rate }}</p>
        </router-link>
        <button @click="addToCart(product)" class="add-to-cart-button">Добавить в корзину</button>
      </div>
    </div>

    <div class="pagination">
      <button @click="prevPage" :disabled="currentPage === 1">Назад</button>
      <button @click="nextPage" :disabled="currentPage === totalPages">Вперед</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      products: [],
      categories: [],
      selectedCategory: '',
      loading: true,
      currentPage: 1,
      pageSize: 10
    }
  },
  computed: {
    totalPages () {
      return Math.ceil(this.filteredProducts.length / this.pageSize)
    },
    paginatedProducts () {
      const start = (this.currentPage - 1) * this.pageSize
      return this.filteredProducts.slice(start, start + this.pageSize)
    },
    filteredProducts () {
      return this.selectedCategory
        ? this.products.filter(product => product.category === this.selectedCategory)
        : this.products
    }
  },
  methods: {
    async fetchProducts () {
      this.loading = true
      try {
        const response = await axios.get('https://fakestoreapi.com/products')
        this.products = response.data
      } catch (error) {
        if (error.response) {
          console.error('Error status:', error.response.status)
          console.error('Error data:', error.response.data)
        } else if (error.request) {
          console.error('Error request:', error.request)
        } else {
          console.error('Error message:', error.message)
        }
        alert('Ошибка загрузки товаров. Пожалуйста, проверьте консоль для получения дополнительной информации.')
      } finally {
        this.loading = false
      }
    },
    addToCart (product) {
      const cart = JSON.parse(localStorage.getItem('cart')) || []
      const existingProduct = cart.find(item => item.id === product.id)
      if (existingProduct) {
        existingProduct.quantity += 1
      } else {
        cart.push({ ...product, quantity: 1 })
      }
      localStorage.setItem('cart', JSON.stringify(cart))
    },
    prevPage () {
      if (this.currentPage > 1) {
        this.currentPage--
      }
    },
    nextPage () {
      if (this.currentPage < this.totalPages) {
        this.currentPage++
      }
    }
  },
  mounted () {
    this.fetchProducts()
  }
}
</script>

<style scoped>
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f5f5f5;
}

.home {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.title {
  text-align: center;
  font-size: 2.5em;
  color: #333;
  margin-bottom: 20px;
}

.filter {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.filter-label {
  margin-right: 10px;
  font-weight: bold;
}

.filter-select {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-size: 1em;
}

.loading {
  text-align: center;
  font-size: 1.5em;
  color: #666;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px 0;
}

.product-card {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 15px;
  margin: 10px;
  width: 250px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

.image-wrapper {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
}

.product-image {
  width: 100%;
  height: auto;
  transition: transform 0.3s;
}

.image-wrapper:hover .product-image {
  transform: scale(1.1);
}

.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-wrapper:hover .image-overlay {
  opacity: 1;
}

.overlay-text {
  color: white;
  font-size: 1.2em;
  text-align: center;
}

.product-title {
  font-size: 1.2em;
  margin: 10px 0;
  color: #333;
}

.product-price,
.product-category,
.product-rating {
  margin: 5px 0;
  color: #555;
}

.add-to-cart-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 1em;
  width: 100%;
}

.add-to-cart-button:hover {
  background-color: #218838;
}

.pagination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.pagination button {
  margin: 0 5px;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination button:hover:not(:disabled) {
  background-color: #0056b3;
}
</style>
